<template>
   <div class="input-container">
      <textarea 
        v-model="userInput" 
        @keyup.enter="sendMessage"
        placeholder="Choose your topic to get a lesson.."
        :disabled="loading"
      ></textarea>
      <button @click="sendMessage" :disabled="loading">
        {{ loading ? 'Sending...' : 'Send' }}
      </button>
    </div>
  <div class="chat-container">
    <div class="chat-messages" ref="messageContainer">
      <div v-for="(message, index) in messages" 
           :key="index" 
           :class="['message', message.role]">
        <div class="message-content">{{ message.content }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ChatGPT',
  data() {
    return {
      messages: [],
      userInput: '',
      loading: false,
      apiKey: 'sk-ASNY7Zgcmc66oV07WigiFoxlY3u8a1VgC9mRqXhr9XMFg4W1',
      apiUrl: 'https://api.chatanywhere.tech/v1/chat/completions'
    }
  },
  methods: {
    getSystemMessage(userInput) {
      const input = userInput.toLowerCase();
        return `You are a Chinese language teacher. Create a comprehensive Chinese language lesson plan for the topic "${input}" following this format:
              PART 1 - STORY:
              Create a simple story about "${input}" in Chinese.
              First, provide the entire story written in Chinese.
              Use only HSK Level 3 or lower vocabulary.
              Then, present the story again with formatting requirements:
              After each line of Chinese text, provide:
              Pinyin (the phonetic transcription).
              English translation.
              Each element (Chinese, Pinyin, English) should be on a new line.
              At the end of the story:
              Provide the entire story translated into English.
              
              PART 2 - COMPREHENSION QUESTIONS:
                Create 5 multiple-choice questions based on the story.
                Each question should be written in Chinese.
                Provide Pinyin for each question.
                Options:
                Each question should have 4 options (A, B, C, D).
                Clearly mark the correct answer for each question.
              PART 3 - FILL IN THE GAPS:
              - Create 5 sentences from the story with missing words
              - Provide a word bank with all missing words
              - Format gaps as _____ 
              - Include both Chinese characters and pinyin for word bank
              PART 4 - VOCABULARY LIST:
              - List all key vocabulary from the story
              -each vocabulary maust have example sentence 
              - Format: Chinese(pinyin) - English meaning
              Keep all content simple and beginner-friendly.
              PART 5 - convaersation topic :
              -make a simple short conversaition topic based on the story in chinese
              `;
     
    },

    async sendMessage(e) {
      if (e.key === 'Enter' && e.shiftKey) return;
      if (e.type === 'keyup') e.preventDefault();
      
      if (!this.userInput.trim() || this.loading) return;
      
      this.messages.push({
        role: 'user',
        content: this.userInput.trim()
      });
      console.log(this.userInput);

      this.loading = true;

      try {
        const response = await axios.post(this.apiUrl, {
          model: 'gpt-3.5-turbo',
          messages: [
            { 
              role: 'system', 
              content: this.getSystemMessage(this.userInput)
            },
            ...this.messages
          ]
        }, {
          headers: {
            'Authorization': `Bearer ${this.apiKey}`,
            'Content-Type': 'application/json'
          }
        });

        this.messages.push({
          role: 'assistant',
          content: response.data.choices[0].message.content
        });
      } catch (error) {
        console.error('Error:', error);
        this.messages.push({
          role: 'assistant',
          content: 'Sorry, I encountered an error. Please try again.'
        });
      } finally {
        this.loading = false;
        this.$nextTick(() => {
          this.scrollToBottom();
        });
      }
      
      this.userInput = '';
    },
    scrollToBottom() {
      const container = this.$refs.messageContainer;
      container.scrollTop = container.scrollHeight;
    }
  }
}
</script>

<style scoped>
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}
.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background: #f5f5f5;
  border-radius: 10px;
  margin-bottom: 20px;
}
.message {
  margin-bottom: 15px;
  padding: 10px 15px;
  border-radius: 10px;
  max-width: 80%;
}
.message.user {
  background: #007bff;
  color: white;
  margin-left: auto;
}
.message.assistant {
  background: white;
  color: #333;
  margin-right: auto;
}
.input-container {
  display: flex;
  gap: 10px;
}
textarea {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  resize: vertical;
  min-height: 50px;
  max-height: 150px;
}
button {
  padding: 10px 20px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
button:disabled {
  background: #ccc;
  cursor: not-allowed;
}
.message-content {
  white-space: pre-wrap;
  word-wrap: break-word;
}
</style>